}
#mainImg > div > img {
    position: absolute;
 /* Full height */
    height: 95vh;
    width: 100%;

    background-position: 50% 50%;
/* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background: rgb(80, 138, 83);
    -webkit-object-fit: cover;
    -moz-object-fit: cover;
    object-fit: cover;
    
}

